.TableView {
  background-color: #E0E1DD;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  padding: 5px;
  overflow: auto;
}

table {
  /* 
  "In the collapsing border model, it is possible to specify borders that 
  surround all or part of a cell, row..." 
  - "https://www.w3.org/TR/CSS21/tables.html#borders" 
  */
  border-collapse: collapse;
  margin: auto;
}

table tr {
  text-align: center;
  border-bottom: 1px solid black;
  background-color: white;
}

table td {
  height: 45px;
}

table thead tr:first-child {
  background-color: inherit;
}

table th {
  background-color: #D1495B;
  width: 5em;
  color: white;
}

table th:first-child {
  border-top-left-radius: 10px;
  width: 5em;
}

table th:last-child {
  border-top-right-radius: 10px;
  width: 5em;
}

input[type="checkbox"] {
  position: absolute;
  visibility: hidden;
}

.td-flex-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.check {
  display: inline-block;
  height: 1em;
  width: 1em;
}

.check svg {
  display: block;
  box-sizing: border-box;
  height: 1em;
  width: 1em;
  fill: none;
  border: 1px solid black;
}

input[type="checkbox"]:checked + label svg{
  background-color: #EDAE49;
  fill: rgb(0, 0, 0);
}

table tr:last-child button {
    border: none;
    border-radius: 5px;
    padding: 0;
    background-color: inherit;
}

table tr:last-child button:hover {
  padding: 0;
  border: none;
  background-color: rgb(208, 208, 208);
  

}
table tr:last-child button:active {
  background-color: rgb(177, 177, 177);
}

table tr:last-child button svg {
  fill: #32D89C;
  height: 1.25em;
  width: 1.25em;
  vertical-align: middle;
}

table tr:last-child button:hover svg {
 fill: green;
}
